
/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/


.site-footer {
	border-top:solid 1px var(--bordure);
	font-size: 1.8rem;
	padding:2.3rem 1rem;
	position: relative;

	
	.backtotop {
		align-items: center;
		background-color: var(--fond);
		display: flex;
		justify-content: center;
		height:4.5rem;
		position: absolute;
		right:1rem;
		top : -5rem;
		width:4.5rem;
		z-index:1;

		svg {
			max-width: 1rem;
		}

		&:hover, &:focus {
			svg {
				path {
					fill:var(--bleu);
				}
			}
		}

		@include bp(lg) {
			right:var(--padding-x);
			top:-5rem;
		}

	}

	.copyright {
		text-align: center;
	}

	ul {
		list-style: none;
		padding:0;
		margin:0;
		li::before {
		display: none;
		}
	}

	a {
		color:var(--bleu);
		display: inline-block;
		font-weight: 400;
		text-decoration: none;
		transition-duration: 0.3s;
		&:hover, &:focus {
			color:var(--bleu);
			text-decoration: underline;
		}
	}

	

	@include bp(lg) {
		.copyright {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
		}

		ul {
			display: flex;
		

			li {
				position: relative;
				&::before {
					content: ''; 
					color: var(--bleu);
					display: inline-block; 
					width: 1ch;
					margin: 0 1ch;
				}
			}

		}
	}
}


