.archive-description {
	margin-bottom: 5rem;
	padding:0 var(--padding-x);
	@include bp(xl) {
		margin-left: auto;
		margin-right: auto;
		max-width: 1024px;
		padding:0;
	}
}
.products {
	margin-bottom:3rem;
	position: relative;
	.product-grid {
		margin-bottom: 1rem;
		padding:0 var(--padding-x);
	}

	&.liste-filtrable {
		padding:0 var(--padding-x);
		.product-grid {
			padding:0;
		}
		&#liste-filtrable-watches {
			background-color: var(--fond);
		}
	}
	&.acf.page-products {
		padding:0;
		.product-grid {
			padding:0;
		}
		.pagination {
			display: none;
		}
	}
	&.acf.home-products {
		padding:3rem 0 5rem;
		&:nth-of-type(2n+1) {
			background-color:var(--fond);
		}
		.title, .above-title {
			text-align: center;
		}
		.above-title {
			font-size: 1.8rem;
			opacity: 0.2;
			text-transform: uppercase;
			@include bp(lg) {
				font-size: 3rem;
			}
		}
	}
	@include bp(lg) {
		margin-bottom:9rem;
		.product-grid {
			margin-bottom:4rem;
		}
		&.liste-filtrable {
			display: flex;
			.filtre {
				flex-basis:25%;
				padding-right: 1.6rem;
			}
			.product-grid {
				flex-basis:75%;
			}
		}
		&.acf.page-products {
			flex-wrap: wrap;
			
			.text-center {
				flex-basis:100%;
			}
			&.avec-produits h2 {
				@include screen-reader-text;
			}
		}
	}

	.browse-all {
		//@include fs(1.5rem,1.9rem);
		font-size: 1.5rem;
		@include bp(lg) {
			font-size: 1.9rem;
		}
		color:var(--bleu);
		font-weight: 300;
		span {
			font-weight: 700;
		}
	}

	//Sur les archives de dernier niveau, masquer la catgorie dans la vignette produit (c'est la mme pour toutes les vignettes)
	.last-cat .card-cat {
		display: none;
	}
}